.starfield {
  width: 500px;
  height: 100%;
  position: relative;
  z-index: 2;
}

.star {
  width: 12px;
  height: 12px;
  position: absolute;
}
.star:before, .star:after {
  content: "";
  display: block;
  width: 20%;
  height: 100%;
  border-radius: 20px;
  background: #FFF;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(1);
}
.star.alt:before, .star.alt:after {
  background: #FFF;
}
.star.alt2:before, .star.alt2:after {
  background: #FFF;
}
.star:after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.sparkle {
  width: 7px;
  height: 7px;
  position: absolute;
  border-radius: 50%;
  background: #FFF;
  transform: scale(1);
  width: 5px;
  height: 5px;
}
.sparkle.alt {
  background: #FFF;
}
.sparkle.alt2 {
  background: #FFF;
}
.small {
  width: 2px;
  height: 2px;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(0.75);
  }
  50% {
    transform: scale(0.5);
  }
  75% {
    transform: scale(0.75);
  }
  100% {
    transform: scale(1);
  }
}
.pulse {
  animation: pulse 1s linear infinite;
}

.pulse-1 {
  animation: pulse 1.1s 300ms linear infinite;
}

.pulse-2 {
  animation: pulse 0.8s 600ms linear infinite;
}

.pulse-3 {
  animation: pulse 1.3s 900ms linear infinite;
}

